.importing-request {
  width: 100%;
  padding: 10px;
  padding-top: 60px;
  height: 100vh;
  background: #ff8220;
  overflow: hidden;

  $boxShadow0: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.12);
  $boxShadow1: 0 1px 2px rgba(0, 0, 0, 0.2);
  $boxShadow2: 0 2px 5px rgba(0, 0, 0, 0.16), 0 2px 5px rgba(0, 0, 0, 0.13);
  $textShadow1: 1px 1px 2px rgba(0, 0, 0, 0.4);
  $borderRadius0: 3px;
  $borderRadius1: 5px;
  $borderRadius2: 7px;
  $fontSize: 12px;

  .block-style {
    background: white;
    border-radius: $borderRadius1;
    border-top-left-radius: $borderRadius1;
    border-top-right-radius: $borderRadius1;
    box-shadow: $boxShadow2;
    height: 100%;
    width: 100%;
  }

  .styled-scrollbar {
    &::-webkit-scrollbar {
      width: 5px;
      height: 5px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: rgba($color: #000000, $alpha: 0.2);
      border-radius: $borderRadius1;
    }
  }

  .importing-request__container {
    width: 100%;

    .importing-request__container__block {
      @extend .block-style;
      padding: 10px;
      height: 90.5vh;

      .importing-request__container__block__header {
        height: 7%;
        display: flex;
        align-items: center;
        padding-left: 2%;
        position: relative;
        font-size: $fontSize;

        $todoColor: #ff8220;
        $acceptedColor: #40a5ec;
        $finishiedColor: #4bb543;

        .importing-request__container__block__header__title {
          font-weight: bold;

          &.--pending {
            color: $todoColor;
          }

          &.--accepted {
            color: $acceptedColor;
          }

          &.--finished {
            color: $finishiedColor;
          }
        }

        .importing-request__container__block__header__btn-update {
          font-size: $fontSize;
          border: 0;
          box-shadow: none;
          margin-left: 5px;
          color: gray;
          background: none;
        }

        .importing-request__container__block__header__number {
          position: absolute;
          right: 0;
          min-width: 40px;
          color: white;
          text-align: center;
          font-weight: bold;
          font-size: $fontSize;
          padding: 2px;
          border-radius: 24px;

          &.--pending {
            background: $todoColor;
          }

          &.--accepted {
            background: $acceptedColor;
          }

          &.--finished {
            background: $finishiedColor;
          }
        }
      }

      .importing-request__container__block__body {
        height: 93%;
        background: #eee;
        border-radius: $borderRadius0;
        padding: 5px;
        overflow: auto;

        &.--pending {
          height: 84.5%;
        }
      }
    }
  }
}
